  drawStateWiseChart(actualStateWiseData){
    let stateWiseData = {...actualStateWiseData};
    let sortedData = [];
    for (let  d in stateWiseData) {
      sortedData.push(stateWiseData[d]);
    }
    sortedData.sort(function(a, b) {
      return b["active"] - a["active"];
    });
    let filteredSortedData = [...sortedData];
    filteredSortedData = _.reject(filteredSortedData, function(d){ return d["active"] ===0 });
    let categories = _.pluck(filteredSortedData, 'state');
    let activeCases = _.pluck(filteredSortedData, 'active');
    this.chartOptions ={
      chart: {
        type: 'column'
    },
    title: {
        text: 'Current active COVID-19 cases'
    },
    xAxis: {
        categories: categories
    },
    yAxis: {
      min: 0,
      title: {
          text: 'Number of Active cases'
      }
    },
    tooltip: {
       pointFormat: 'Active cases: <b>{point.y:.0f}</b>'
    },
    legend: {
      enabled: false
    },
    credits: {
        enabled: true
    },
    series: [{
        name: 'States',
        data: activeCases
    }]
    }
  
  }

